<template>
        <div class="form">
            <form @submit.prevent="submit">
            <h3>Название поста</h3>
            <input type="text" maxlength="100" class="input-title" v-model="title">
            <h3>Краткое описание поста</h3>
            <input type="text" maxlength="150" class="input-description" v-model="description">
            <h3>Описание поста</h3>
            <textarea type="text" class="input-body" style="resize:none; width: 100%; height: 490px;" v-model="body"></textarea>
            <h3>Картинка поста</h3>
            <input type="file">
            <p><button type="submit" class="input-button">Создать</button></p>
        </form>
        </div>
        <!-- <div v-else> <router-link class="link" :to="{ name: 'Login' }" >Войти в аккаунт</router-link></div> -->
</template>

<script>
import { mapMutations } from 'vuex'
export default { 
    data() {
        return {
            title: '',
            body: '',
            description: '',   
            src:'',
        }
    },
    // computed : {
    //     isLoggedIn : function(){ return this.$store.getters.isLoggedIn}
    // },
    methods: {
        ...mapMutations(['createPost']),
        submit() {
             this.createPost({
                 id: Date.now(),
                 title: this.title,
                 description: this.description,
                 body: this.body,
                 src: this.src,  
             })
             this.title = this.body = this.description = '';
        },        
    },
}
</script>

<style lang="scss" scoped>
form{
    background: white;
    display: flex;
    flex-direction: column;
    margin: 10px;
    box-shadow: 0px 3px 7px 0px;
    height: auto;
    padding: 15px;
    align-items: center;
    input{
        margin-bottom: 20px;
        width: 100%;  
    }

    .input-title{
            padding: 10px;
        }
        .input-description{
            padding: 10px;
        }
        .input-body{
            padding: 10px;
        }

        .input-button{
            padding: 10px;
        width: 300px;
        font-size: 20px;
        cursor: pointer;
        }
}
</style>